/*global chrome*/
import React, { Component } from 'react'
import TagsPaper from './tagsPaper'
import CurrentTagPaper from './currentTagPaper'
import ApiCall from './Backend/apiCall';

export default class Tags extends Component {
    constructor(props) {
        super(props);
        this.state = {
            tags: ["apple"],
            savedTags: []
        };
        this.getTags = this.getTags.bind(this);
        this.getChromeStoragefunction = this.getChromeStoragefunction.bind(this)
    }

    componentDidMount() {
        this.getChromeStoragefunction()
    }
    getChromeStoragefunction() {
        chrome.storage.sync.get({ tags: [] }, function (result) {
          var tags = result.tag;
          if (typeof tags === "undefined") {
            this.setState({
                savedTags: [""]
            })
          } else {
            let tempArr = [...result.tags]
            tempArr.tags.map(e => { console.log(e); })
            this.setState({
                savedTags: tempArr
            })
          }
        });
        chrome.storage.onChanged.addListener(function (changes, namespace) {
          for (var key in changes) {
            var storageChange = changes[key];
            storageChange.newValue.map(e => { console.log("new values " + e) })
            // console.log('Storage key "%s" in namespace "%s" changed. ' +
            //             'Old value was "%s", new value is "%s".',
            //             key,
            //             namespace,
            //             storageChange.oldValue,
            //             storageChange.newValue);
          }
          return storageChange.newValue;
        });
        
    }
    getTags(e) {
        this.setState({
            tags: e
        })
        console.log(e)
    }

    render() {

        return (
            <div >
                <p>
                    Get recommendations for articles that matches your
                    interests when you follow more tags:
                </p>
                <b>
                    You are following
                </b>

                <TagsPaper></TagsPaper>
                <b>
                    From this article
                </b>
                
                <CurrentTagPaper tagArray={this.state.tags} />
                <ApiCall getTags={this.getTags}  tags={this.state.tags}/>
            </div>
        )
    }
}
